*,::after,::before{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --primary-color: #011b38;
  --secondary-color: #083a74;
  --cl-white: #FFF;
  --btn-bg: #0d6efd;
  --cl-secondary: #a89797;
  --cl-bg2: rgba(255, 255, 255, .2);
  --transition: all 400ms ease-in-out;
}

body{
  background: var(--primary-color);
  color: var(--cl-white);
  font-family: Arial, Helvetica, sans-serif;
  height: 100vh;
}

a{
  text-decoration: none;
}

li{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
  display: block;
}

.window-scroll{
  background: var(--secondary-color);
  box-shadow: 0 1rem 2rem rgba(0,0,0,0.2);

}